.menu{
    display: flex;
    flex-direction: column;
    background: aliceblue;
    padding: 2em 0;
}
.menu a{
    text-decoration: none;
    margin: .1em auto;
    font-size: 1.4em;
    color: cadetblue;
}

.menu a:hover{
    color: darkcyan;
}

.menu a.active{
    color: darkcyan;
}